.gallerybox{
    width: 100%;
    min-height: 550px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.gallery{
    font-family: 'Montserrat';
font-style: normal;
font-size: 48px;
padding: 30px 0 10px 0;
}
.gallimgs{
    width: 100%;
    min-height: 390px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.gallimginner{
    width: 80%;
    min-height: 370px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}
.gallimg{
    border-radius: 10px;
    width: 30%;
    min-width: 300px;
}
