*{
  box-sizing: border-box;
}
.App{
  width: 100%;
  overflow-x: hidden;
}
.benefits{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 400px;
}
.cards{
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}